import { Box, Button, Slider, Stack, Typography } from "@mui/material";
import React from "react";
import Font from "react-font";
import { HexColorPicker } from "react-colorful";
import { useDispatch, useSelector } from "react-redux";
import { frontStyledText } from "../Actions";
const Controls = () => {
  // const [txtStyle, setTxtStyle] = React.useState({
  //   fntSize: 1,
  //   ltrSpace: 1,
  //   rt: 0,
  //   color: "#fff",
  // });
  // const { fntSize, ltrSpace, rt, color } = txtStyle;

  const dispatch = useDispatch();
  const { FrontStyling } = useSelector((state) => state);
  const { text } = FrontStyling;
  const { value, fntSize, ltrSpace, rt, color, font } = text;

  return (
    <Stack display="flex" direction="column" spacing={2} sx={{ width: "100%" }}>
      <Font family={font}>
        <p
          style={{
            color,
            fontSize: fntSize !== 1 && fntSize + "rem",
            letterSpacing: ltrSpace !== 1 && ltrSpace + "px",
            transform: rt !== 0 && `rotate(${rt}deg)`,
            transition: "all 0.35s",
          }}
        >
          {value}
        </p>
      </Font>

      <Box sx={{ width: "100%" }}>
        <Typography fontWeight="bold" variant="h6" fontSize="1.1rem">
          Font Size: {fntSize === 1 ? "default" : fntSize}
        </Typography>
        <Slider
          value={fntSize}
          size="small"
          min={1}
          max={4}
          aria-label="Small"
          valueLabelDisplay="auto"
          sx={{ color: "darkorange" }}
          onChange={(e) =>
            dispatch(
              frontStyledText({
                ...text,
                fntSize: e.target.value,
              })
            )
          }
        />
      </Box>

      <Box sx={{ width: "100%" }}>
        <Typography fontWeight="bold" variant="h6" fontSize="1.1rem">
          Letter-Spacing: {ltrSpace === 1 ? "default" : ltrSpace}
        </Typography>
        <Slider
          value={ltrSpace}
          size="small"
          min={1}
          max={8}
          aria-label="Small"
          valueLabelDisplay="auto"
          sx={{ color: "darkorange" }}
          onChange={(e) =>
            dispatch(
              frontStyledText({
                ...text,
                ltrSpace: e.target.value,
              })
            )
          }
        />
      </Box>

      <Box sx={{ width: "100%" }}>
        <Typography fontWeight="bold" variant="h6" fontSize="1.1rem">
          Rotate: {rt === 0 ? "0deg" : rt + "deg"}
        </Typography>
        <Slider
          value={rt}
          size="small"
          min={0}
          max={360}
          aria-label="Small"
          valueLabelDisplay="auto"
          sx={{ color: "darkorange" }}
          onChange={(e) =>
            dispatch(
              frontStyledText({
                ...text,
                rt: e.target.value,
              })
            )
          }
        />
      </Box>

      <Box sx={{ width: "100%" }}>
        <Typography fontWeight="bold" variant="h6" fontSize="1.1rem">
          color: {color}
        </Typography>
        <HexColorPicker
          color={color}
          onChange={(c) =>
            dispatch(
              frontStyledText({
                ...text,
                color: c,
              })
            )
          }
        />
      </Box>
    </Stack>
  );
};

export default Controls;
