import {
  Close,
  EmojiFlags,
  Map,
  Pets,
  PhotoLibraryOutlined,
  Public,
  StickyNote2,
  Undo,
} from "@mui/icons-material";
import { Box, Grid, IconButton, Stack, Typography } from "@mui/material";
import { flags, map, globe, sticker, animals } from "../Assets/flags/flags";
import React from "react";

// const images = [
//   "https://upload.wikimedia.org/wikipedia/commons/3/3b/Javascript_Logo.png?20210407134359",
//   "https://reactjs.org/logo-og.png",
//   "https://images.ctfassets.net/23aumh6u8s0i/4TsG2mTRrLFhlQ9G1m19sC/4c9f98d56165a0bdd71cbe7b9c2e2484/flutter",
//   "https://images.unsplash.com/photo-1516259762381-22954d7d3ad2?ixlib=rb-4.0.3&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=1189&q=80",
//   "data:image/png;base64,",
// ];

const { mexico, germany, italy, canada, turkey } = flags;
const { coop, peace } = globe;
const { usSticker, madeInUsa } = sticker;
const { lebanon, lebanonTwo } = map;
const { cat, dog } = animals;

const allFlags = [mexico, germany, italy, canada, turkey];
const allGlobe = [coop, peace];
const allStickers = [usSticker, madeInUsa];
const allAnimals = [cat, dog];
const country = [lebanon, lebanonTwo];
const allPhotos = [
  ...allAnimals,
  ...allFlags,
  ...allGlobe,
  ...allStickers,
  ...country,
];
const Images = () => {
  const [controls, setControls] = React.useState({ bool: false, font: "" });
  const [ImagePage, setImagPage] = React.useState();
  const CategoryGrid = () => {
    return (
      <Grid container gap={4}>
        <Grid item>
          <IconButton
            size="large"
            sx={{
              "&:hover": { backgroundColor: "gray", color: "orange" },
              transition: "all 0.35s",
            }}
            onClick={() => setImagPage(allFlags)}
          >
            <EmojiFlags fontSize="large" />
          </IconButton>
        </Grid>

        <Grid item>
          <IconButton
            size="large"
            sx={{
              "&:hover": { backgroundColor: "gray", color: "orange" },
              transition: "all 0.35s",
            }}
            onClick={() => setImagPage(allGlobe)}
          >
            <Public fontSize="large" />
          </IconButton>
        </Grid>

        <Grid item>
          <IconButton
            size="large"
            sx={{
              "&:hover": { backgroundColor: "gray", color: "orange" },
              transition: "all 0.35s",
            }}
            onClick={() => setImagPage(country)}
          >
            <Map fontSize="large" />
          </IconButton>
        </Grid>

        <Grid item>
          <IconButton
            size="large"
            sx={{
              "&:hover": { backgroundColor: "gray", color: "orange" },
              transition: "all 0.35s",
            }}
            onClick={() => setImagPage(allStickers)}
          >
            <StickyNote2 fontSize="large" />
          </IconButton>
        </Grid>

        <Grid item>
          <IconButton
            size="large"
            sx={{
              "&:hover": { backgroundColor: "gray", color: "orange" },
              transition: "all 0.35s",
            }}
            onClick={() => setImagPage(allAnimals)}
          >
            <Pets fontSize="large" />
          </IconButton>
        </Grid>

        <Grid item>
          <IconButton
            size="large"
            sx={{
              "&:hover": { backgroundColor: "gray", color: "orange" },
              transition: "all 0.35s",
            }}
            onClick={() => setImagPage(allPhotos)}
          >
            <PhotoLibraryOutlined fontSize="large" />
          </IconButton>
        </Grid>
      </Grid>
    );
  };

  const ImageGrid = ({ ImagesToRender }) => {
    return (
      <Grid
        container
        gap={2}
        alignItems="center"
        justifyContent="center"
        sx={{ pt: 5 }}
      >
        {ImagesToRender?.map((img, idx) => {
          return (
            <Grid
              key={idx}
              item
              sx={{
                "&:hover": { transform: "scale(1.1)", cursor: "pointer" },
                img: { width: "100%" },
              }}
            >
              <img src={img} alt="img" />
            </Grid>
          );
        })}
      </Grid>
    );
  };

  return (
    <Stack display="flex" direction="column" spacing={2}>
      <Stack
        flex={1}
        display="flex"
        direction="row"
        justifyContent="space-between"
        alignItems="center"
        bgcolor={"#333333"}
      >
        <Box flex={1}>
          <Typography textAlign="center" fontWeight="bold" color="white">
            Add Image
          </Typography>
        </Box>
        <IconButton
          onClick={() =>
            setControls((prevValue) => {
              return { ...prevValue, bool: false };
            })
          }
          size="small"
          sx={{ display: controls ? "inline-block" : "none" }}
        >
          <Undo sx={{ color: "orange", "&:hover": { color: "darkorange" } }} />
        </IconButton>
        <IconButton size="small">
          <Close sx={{ color: "gray", "&:hover": { color: "darkgray" } }} />
        </IconButton>
      </Stack>
      <CategoryGrid />
      <ImageGrid ImagesToRender={ImagePage} />
    </Stack>
  );
};

export default Images;
